<template>
    <div>
      <div class="wrap">
      <a @click="$router.go(-1)"><</a>
      <div class="content">
        <h2>修改密码</h2>
      </div>
    </div>
      <ul>
        <li>
          <i class="iconfont  icon-suo6"></i><input type="password" placeholder="原来的密码">
        </li>
        <li>
          <i class="iconfont  icon-suo6"></i><input type="password" placeholder="新密码（6-16位）">
        </li>
      </ul>
      <div class="sure">
        <div>
        <p>提交</p>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
      name: "changepassword"
    }
</script>

<style scoped>
.wrap{
  background: #452E94;
  width:100%;
  height: 1.11rem;
  display:flex;
  justify-content: space-between;
}
.wrap a{
  display:block;
  font-size:0.6rem;
  margin:0.16rem;
  height: 0.56rem;
  width: 1.2rem;
  border-radius:2rem;
  text-align:center;
  background: #452E94;
  color:#e6d9f3;
}
.content{
  width: 100%;
}
.content h2{
  color: #E0E0EA;
  text-align: center;
  font-size: 0.35rem;
  line-height:1.11rem;
  padding-right:1.2rem;
}
  input{
    border:0;
    margin-left: 0.2rem;
    font-size: 0.3rem;
  }
  i{
    color:#CCCCCC;
    font-size: 0.32rem;
  }
  ul{
    margin-top: 0.3rem;
    background: white;
  }
  li{
    padding: 0.3rem 0.4rem;
    border-bottom: 1px solid #EAEAEA;
  }
  .sure{
    padding: 0.3rem 0.4rem 0 0.4rem;
  }
.sure div{
  text-align: center;
  color:white;
  background: #CCCCCC;
  border-radius: 4px;
  font-size: 0.34rem;
  width: 100%;
  height: 1rem;
  line-height: 1rem;
  letter-spacing: 10px;
}
</style>



