<template>
  <div>
    <div class="wrap">
      <a @click="$router.go(-1)"><</a>
      <div class="content">
        <h2>消息</h2>
      </div>
    </div>
    <div class="choose">
      <p v-show="ensure" @click="cli">编辑</p>
      <p v-show="!ensure" @click="cli">取消</p>
    </div>
    <div style="margin: 0 auto;width: 50%;">
      <img src="../../../static/img/tiqiu.png" alt="" >
    </div>
    <div v-show="!ensure"  class="option">
      <ul>
        <li><p>全选</p></li>
        <li><p>反选</p></li>
        <li  @click="cli"><p>删除</p></li>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    name: "myinformation",
    data() {
      return {
        ensure: true
      }
    },
    methods: {
      cli(){
        this.ensure = !this.ensure
      }
    }
  }
</script>

<style scoped>
.wrap{
  background: #452E94;
  width:100%;
  height: 1.11rem;
  display:flex;
  justify-content: space-between;
}
.wrap a{
  display:block;
  font-size:0.6rem;
  margin:0.16rem;
  height: 0.56rem;
  width: 1.2rem;
  border-radius:2rem;
  text-align:center;
  background: #452E94;
  color:#e6d9f3;
}
.content{
  width: 100%;
}
.content h2{
  color: #E0E0EA;
  text-align: center;
  font-size: 0.35rem;
  line-height:1.11rem;
  padding-right:1.2rem;
}
  body{
    background: #EAEAEA;
  }
  .option{
    width: 100%;
    height: 1.5rem;
    padding: 0.26rem  0.5rem 0 0.5rem;
    background: white;
    position: fixed;
    bottom:0;
    border-top: 1px solid #E9E9E9;
  }
  .option ul{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
  }
  li{
    float: left;
    height: 0.9rem;
    width: 31%;
    background: #EAEAEA;
    border-radius: 3px;
    box-shadow: 1px 1px 1px 1px #CCC;
    font-size: 0.26rem;
    text-align: center;
    line-height: 0.9rem;
    color:#5F5F5F;
    font-weight: 600;
  }
.choose{
  width: 1.12rem;
  height: 0.56rem;
  border-radius: 20px;
  border: 1px solid #EAEAEA;
  text-align: center;
  line-height: 0.56rem;
  font-size: 0.28rem;
  color:white;
  position: absolute;
  right: 0.2rem;
  top: 0.3rem;
}
</style>



